import {mapGetters, mapState} from 'vuex';
import appBanner from '../../../components/page-component/goods/app-goods-banner.vue';
import appShareQrCode from '../../../components/page-component/app-share-qr-code-poster/app-share-qr-code-poster.vue';
import bdDetail from '@/components/page-component/goods/bd-detail.vue';

export default {
    name: 'goods',
    data() {
        return {
            shareShow: false,
            goods: {},
            url: this.$api.exchange.qrcode,
            show: false,
            list: [],
            code: null,
            id: null,
            status: '',
            windowHeight: 667,
            loading: false,
            first: true,
        }
    },
    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        that.code = options.code;
        that.windowHeight = uni.getSystemInfoSync().windowHeight;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.request({
            url: that.$api.exchange.me_detail,
            data: {
                code: that.code,
            }
        }).then(response => {
            if(response) {
                that.first = false;
                let { goods_attr, code,status } = response;
                that.url = that.url + '&code=' + code;
                that.goods = goods_attr;
                that.code = code;
                that.status = status;
                that.loading = true;
                that.$hideLoading();
            }
        });
    },
    onShow() {
        if(this.first) {
            return false
        }
        this.$showLoading();
        this.$nextTick(() => {
            let that = this;
            that.request({
                url: that.$api.exchange.me_detail,
                data: {
                    code: that.code,
                }
            }).then(response => {
                let { goods_attr, code,status } = response;
                that.goods = goods_attr;
                that.code = code;
                that.status = status;
                that.loading = true;
                that.$hideLoading();
            });
        })
    },
    computed: {
        ...mapState('gConfig',{
            iphone: (data) => {
                return data.iphone;
            },
            iphoneHeight: (state) =>{
                return  state.iphoneHeight;
            },
        }),
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        }),
        ...mapState({
            mall: state => state.mallConfig.mall,
            userInfo: state => state.user.info
        })
    },

    onShareAppMessage() {
        return this.hShareAppMessage();
    },

    methods: {
        hShareAppMessage(s = false) {
            return this.$shareAppMessage({
                path: '/plugins/exchange/gift/gift',
                title: this.goods.app_share_title ? this.goods.app_share_title : this.goods.name,
                imageUrl: this.goods.app_share_pic ? this.goods.app_share_pic : '',
                params: {
                    code: this.code,
                }
            }, s);
        },
        copy() {
            this.$utils.uniCopy({
                data: this.code,
                success() {



                },
            });
        },
        toExchange() {
            uni.navigateTo({
                url: '/plugins/exchange/detail/detail?code=' + this.code
            })
        },
        async request({url, data}) {
            const response = await this.$request({
                url: url,
                data: data,
            });
            if (response.code === 0) {
                return response.data;
            }else {
                uni.showModal({
                    title: '提示',
                    content: response.msg,
                    showCancel: false,
                    success: function (res) {
                        uni.navigateBack();
                    }
                });
            }
        }
    },
    components: {
        'app-banner': appBanner,
        'app-share-qr-code': appShareQrCode,
        bdDetail
    }
}
